.skills {
  & {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
  }
  &__body {
    margin-top: 40px;
    display: grid;
    grid-template-columns: 450px 450px;
    grid-gap: 10px;
    width: 100%;
    align-items: center;
    justify-content: center;
  }
  &__row {
    display: flex;
  }
  &__col {
  }
}

@media all and (max-width: $bp-tablet) {
  .skills {
    &__body {
      grid-template-columns: 450px;
    }
  }
}

@media all and (max-width: $bp-mobile) {
  .skills {
    &__body {
      grid-template-columns: 100%;
    }
  }
}
