.home {
  &__body {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 100vh;
  }
  &__social-section {

  }
  &__main-section {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__main-section > :not(:nth-child(1)) {
    margin-top: 15px;
  }
  &__avatar-section .home__social-links {
    display: none;
  }
  &__social-links {
    display: flex;
    flex-direction: column;
  }
  &__social-link:not(:nth-child(1)) {
    margin-top: 20px;
  }
  &__social-icon {
    font-size: 25px;
    color: $color-dark;
    cursor: pointer;
  }
  &__social-icon:hover {
    color: $color-purple;
  }
  &__title-sm {
    font-size: 25px;
    font-weight: 600;
  }
  &__about {
    max-width: 400px;
  }
  &__avatar {
    max-width: 350px;
    border-radius: 50%;
  }
  &__scroll-down {
    margin-top: 50px;
    display: flex;
    align-items: center;
    color: var(--text-color-dark);
    font-size: 18px;
    font-weight: 400;
    position: relative;
    animation: {
      name: up-down;
      duration: 1.5s;
      timing-function: easa-in-out;
      fill-mode: forwards;
      iteration-count: infinite;
      direction: alternate-reverse;
    }
  }
  &__scrolll-down > :not(:nth-child(1)) {
    margin-left: 5px;
  }
  &__mouse-icon {
    color: $color-purple;
    font-size: 35px;
  }
  &__arrow-down-icon {
    color: $color-purple;
    font-size: 30px;
  }
}

@keyframes up-down {
  from {
    top: 0px;
  }
  to {
    top: 20px;
  }
}

@media all and (max-width: $bp-tablet) {
  .home {
    &__body {
      flex-direction: column-reverse;
      justify-content: center;
    }
    &__body > * {
      width: 100%;
    }
    &__social-section {
      display: none;
    }
    &__scroll-down {
      display: none;
    }
    &__main-section {
      margin-top: 20px;
    }
    &__avatar-section {
      text-align: center;
      position: relative;
    }
    &__avatar-section .home__social-links {
      display: flex;
      flex-direction: column;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
    &__avatar {
      width: 50%;
    }
  }
}
