*, *::after, *::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  line-height: 1em;
}

html {
  font-family: Poppins;
  font-size: $font-size-base;
  color: $color-mute;
  scroll-behavior: smooth;
}

body.lock {
  overflow: hidden;
}

ul {
  list-style-type: none;
}

p {
  font-weight: 500;
  line-height: 23px;
}

a {
  color: inherit;
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  color: $color-dark;
}

h1 {
  font-size: $text-size-h1;
}

h2 {
  font-size: $text-size-h2;
}

h3 {
  font-size: $text-size-h3;
}

h4 {
  font-size: $text-size-h4;
}

button, input, textarea {
  outline: none;
  background-color: inherit;
  border: none;
}

textarea {
  font-family: inherit;
}

button {
  cursor: pointer;
}

::-webkit-scrollbar {
  width: 7px;
  border-radius: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.439);
}

::-webkit-scrollbar-corner {
  border-radius: 10px;
}
