@mixin b($size: 1, $color: black) {
  border: $size + px solid $color;
}

@mixin center() {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin gutter-h($size: 10px) {
  &:not(:nth-child(1)) {
    margin-left: $size;
  }
}

@mixin gutter-v($size: 10px) {
  &:not(:nth-child(1)) {
    margin-top: $size;
  }
}
