.projects {
  & {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    min-height: 100vh;
  }
  &__body {
    margin-top: 40px;
  }
  &__swiper {
    width: 90%;
    margin: 0 auto;
  }
}

@media all and (max-width: $bp-tablet) {
  .projects {
    &__swiper {
      width: 100%;
    }
  }
}
