.footer {
  & {
    background-color: $color-purple;
    color: $color-light;
  }
  &__body {
    padding: 40px 0 150px;
    display: flex;
    position: relative;
  }
  &__body > * {
    flex-basis: 33%;
    flex-grow: 1;
  }
  &__left {
    display: flex;
    flex-direction: column;
  }
  &__center {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__right {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__logo-lg {
    font-size: 50px;
    font-weight: 600;
  }
  &__logo-sm {
    font-size: 20px;
    margin-top: 10px;
  }
  &__links {
    display: flex;
  }
  &__link {
    font-size: 18px;
    cursor: pointer;
  }
  &__link:not(:nth-child(1)) {
    margin-left: 20px;
  }
  &__link:hover {
    text-decoration: underline;
  }
  &__social-links {
    display: flex;
  }
  &__social-link {
    cursor: pointer;
  }
  &__social-link:not(:nth-child(1)) {
    margin-left: 20px;
  }
  &__social-icon {
    color: $color-light;
    font-size: 25px;
  }
  &__rights {
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    color: rgba(0, 0, 0, 0.57);
    text-align: center;
  }
}

@media all and (max-width: $bp-mobile) {
  .footer {
    &__body {
      flex-direction: column;
    }
    &__body > :not(:nth-child(1)) {
      margin-top: 40px;
    }
    &__center {
      justify-content: flex-start;
    }
    &__right {
      justify-content: flex-start;
    }
    &__links {
      flex-direction: column;
    }
    &__link:not(:nth-child(1)) {
      margin-top: 20px;
      margin-left: 0;
    }
    &__rights {
      bottom: 70px;
    }
  }
}
