.contact {
  & {
    min-height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }
  &__body {
    margin-top: 40px;
    display: flex;
  }
  &__left {

  }
  &__right {
    margin-left: 20px;
  }
  &__methods {
    display: flex;
    flex-direction: column;
  }
  &__method:not(:nth-child(1)) {
    margin-top: 20px;
  }
  &__method {
    display: flex;
    align-items: center;
  }
  &__method-main {
    margin-left: 15px;
    display: flex;
    flex-direction: column;
  }
  &__method-icon {
    color: $color-purple;
    font-size: 35px;
  }
  &__method-text-lg {
    color: $color-dark;
    font-weight: 600;
    font-size: 22px;
  }
  &__method-text-sm {
    font-weight: 500;
    margin-top: 7px;
  }
  &__form {
    display: grid;
    grid-template-columns: 300px 300px;
    grid-template-rows: 70px 70px 100px;
    grid-gap: 20px;
  }
  &__input {
    padding: 20px;
    background-color: $color-mute-2;
    border-radius: 10px;
    font-size: 20px;
  }
  &__input-name {

  }
  &__input-email {

  }
  &__input-phone {
    grid-column: 1 / span 2;
  }
  &__input-message {
    grid-column: 1 / span 2;
    resize: vertical;
  }
  &__send-btn {
    margin-top: 20px;
  }
}

@media all and (max-width: $bp-tablet) {
  .contact {
    &__body {
      flex-direction: column;
      align-items: stretch;
      width: 100%;
    }
    &__left {
    }
    &__right {
      margin: 50px 0 0 0;
    }
    &__form {
      grid-template-columns: auto;
      grid-template-rows: 60px 60px 60px 80px;
    }
    &__input-message, &__input-phone {
      grid-column: 1 / span 1;
    }
  }
}
