.button {
  & {
    padding: 15px 15px;
    display: flex;
    align-items: center;
    border-radius: 7px;
    border: 2px solid $color-purple;
    transition: background-color .2s linear;
    font-weight: 600;
    font-size: 18px;
  }
  &__icon {
    margin-left: 10px;
    font-size: 24px;
  }
  &--color-light {
    background-color: $color-light;
    color: var(--color-primary);
  }
  &--color-light:hover {
    background-color: $color-purple;
    color: $color-light
  }
  &--color-primary {
    background-color: $color-purple;
    color: $color-light;
  }
  &--color-primary:hover {
    background-color: $color-light;
    color: $color-purple;
  }
}

@media all and (max-width: $bp-mobile) {
  .button {
    & {
      padding: 10px 10px;
    }
  }
}
