.shade {
  & {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.481);
    z-index: $z-index-shade;
  }
  &--visible {
    display: block;
  }
}

.bottom-menu {
  & {
    display: none;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
    color: $color-dark;
    z-index: $z-index-bottom-menu;
    background-color: $color-light;
  }
  &__body {
    height: 40px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo {
    font-weight: 900;
  }
  &__right {
    display: flex;
  }
  &__right--hidden {
    display: none;
  }
  &__btn:not(:nth-child(1)) {
    margin-left: 10px;
  }
  &__icon {
    font-size: 22px;
  }
  &__links-panel {
    padding: 20px 0 50px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    background-color: $color-light;
    box-shadow: 0 0 10px rgba(0, 0, 0, .2);
    bottom: 0;
    left: 0;
    right: 0;
    transform: translateY(100%);
    transition: .2s transform linear;
    z-index: $z-index-links-panel;
  }
  &__links-panel--opened {
    transform: translateY(0%);
  }
  &__close-links-btn {
    position: absolute;
    bottom: 10px;
    right: 15px;
    font-size: 30px;
    color: $color-dark;
  }
  &__links {
    width: 80%;
    display: grid;
    grid-template-columns: 33% 33% 33%;
    grid-template-rows: auto auto;
    grid-row-gap: 30px;
  }
  &__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: $color-mute;
  }
  &__link--active {
    color: $color-purple;
  }
  &__link-icon {
    font-size: 23px;
  }
  &__link-text {
    margin-top: 5px;
    font-size: 15px;
    letter-spacing: .5px;
  }
}

@media all and (max-width: $bp-tablet) {
  .bottom-menu {
    & {
      display: block;
    }
  }
}
