.container {
  max-width: 1112px;
  width: 100%;
  padding: 0 10px;
  margin: 0 auto;
}

.section {
  & {
    padding: 20px 0;
  }
}

.section-title {
  & {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__lg {
    font-weight: 700;
    color: $color-dark;
  }
  &__sm {
    font-size: 16px;
    margin-top: 5px;
    font-weight: 600;
    color: $color-mute;
    letter-spacing: .3px;
  }
}
