.skills-category {
  & {
    box-shadow: 0 0 20px rgba(0, 0, 0, .1);
    position: relative;
    border-radius: 5px;
    cursor: pointer;
    height: 80px;
  }
  &__body {
    position: relative;
    padding: 0 10px 0 10px;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
  }
  &__body-left {

  }
  &__body-center {
    margin-left: 15px;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
  &__body-right {
    transition: transform .2s linear;
  }
  &__icon {
    font-size: 40px;
    color: $color-purple;
  }
  &__title-lg {
    font-size: 22px;
  }
  &__title-sm {
    margin-top: 10px;
    font-size: 16px;
    font-weight: 500;
  }
  &__popup {
    padding: 10px 10px 10px 60px;
    position: absolute;
    opacity: 0;
    visibility: hidden;
    top: calc(100% + 30px);
    left: 0;
    width: 100%;
    background-color: $color-light;
    transition: all .4s ease-out;
    border-radius: 10px;
    z-index: $z-index-skills-popup;
    box-shadow: 0 0 20px rgba(0, 0, 0, .1);
  }
  &__arrow-down-icon {
    font-size: 30px;
  }
  & .skills-category__body:hover &__body-right {
    transform: rotateZ(180deg);
  }
  & .skills-category__body:hover + .skills-category__popup {
    visibility: visible;
    opacity: 1;
    top: calc(100% + 10px);
  }
  &__skill {
    display: flex;
    flex-direction: column;
  }
  &__skill:not(:nth-child(1)) {
    margin-top: 20px;
  }
  &__skill-name {
    color: $color-dark;
    font-weight: 600;
    font-size: 20px;
  }
  &__skill-header {
    display: flex;
    justify-content: space-between;
  }
  &__skill-percent {
    font-size: 20px
  }
  &__progress {
    margin-top: 10px;
    height: 7px;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.289);
    border-radius: 15px;
    overflow: hidden;
  }
  &__inner {
    height: 100%;
    background-color: $color-purple;
  }
}

@media all and (max-width: $bp-mobile) {
  .skills-category {
    & {
      max-width: 100%;
      width: 100%;
      height: 70px;
    }
  }
}
