.up {
  & {
    transform: scale(0);
    position: fixed;
    bottom: 20px;
    right: 20px;
    background-color: $color-purple;
    border-radius: 5px;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 0 10px rgba(0, 0, 0, .2);
    cursor: pointer;
    transition: transform .2s linear;
    z-index: $z-index-up-btn;
  }
  &--visible {
    transform: scale(1);
  }
  &__icon {
    color: $color-light;
  }
}

@media all and (max-width: $bp-tablet) {
  .up {
    & {
      bottom: 50px;
      right: 10px;
    }
  }
}
