.about {
  & {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
  }
  &__body {
    margin-top: 40px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__body > * {
    width: 40%;
  }
  &__img-container {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__img {
    max-width: 100%;
    border-radius: 15px;
  }
  &__main {
    margin-left: 30px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__main > :not(:nth-child(1)) {
    margin-top: 30px;
  }
  &__text {
    text-align: start;
  }
  &__info-items {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  &__info-item {
    flex-basis: 33.3333333%;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__info-item-num {
    font-size: 30px;
    font-weight: 500;
    color: $color-dark;
  }
  &__info-item-desc {
    margin-top: 10px;
    color: $color-mute;
    white-space: pre-line;
    text-align: center;
    line-height: 20px;
    font-weight: 500;
  }
}

@media all and (max-width: $bp-tablet) {
  .about {
    &__body {
      flex-direction: column;
    }
    &__body > * {
      margin-left: 0;
      width: 80%;
    }
    &__main {
      margin-top: 20px;
    }
  }
}

@media all and (max-width: $bp-mobile) {
  .about {
    &__body > * {
      width: 100%;
    }
    &__text {
      text-align: center;
    }
    &__main {
      align-items: center;
    }
  }
}
